import uniPopup from '@/components/uni-popup/uni-popup'
export default{
	components:{
		uniPopup
	},
	data(){
		return {
			popupShow:false
		}
	},
	computed: {
		
	},
	onLoad(){
		
	},
	methods:{
		detailLink(val){
			let url = ''
			switch(val){
				case 1:
					this.$refs.popup.open()
					break;
				case 2:
					url = '/sub_adviser/pages/wallet/accountDetail'
					break;
				case 3:
					url = '/sub_adviser/pages/wallet/withdrawDetail'
					break;
			}
			if(url){
				uni.navigateTo({url})
			}
		},
		cancelBtn(){
			this.$refs.popup.close()
		}
	}
}
